.login,
.helpcenter,
.callme,
.customer,
.addye,
.huodongguanli,
.addhuodong,
.bianjihuodong,
.huodong {
  height: 90vh;
  background: linear-gradient(#fff, rgb(78, 78, 78));
}
.huodongye .toubu a {
  font-size: 18px;
  color: rgb(78, 77, 77);
  margin-left: 20px;
  margin-top: 5px;
}
.chakanbaogao {
  width: 69%;
  background-color: #fff;
  margin: 0 auto;
  padding: 10px;
}
.home a {
  color: rgb(75, 74, 74);
}
.chakanbaogao .left div {
  color: rgb(77, 76, 76);
  text-decoration: underline;
  text-align: center;
  margin-top: 10px;
  cursor: pointer;
}
.chakanbaogao .left {
  width: 25%;
  padding: 4px 10px;
  float: left;
  background-color: rgb(231, 230, 230);
}
.chakanbaogao .zongti,
.chakanbaogao .bn {
  background-color: rgb(204, 202, 202);
  border-radius: 4px;
}
.chakanbaogao .right {
  width: 70%;
  float: left;
}
.chakanbaogao .right .title {
  font-size: 20px;
  text-align: center;
  color: rgb(68, 68, 68);
}
.chakanbaogao .right .content {
  text-align: center;
  margin-top: 30px;
}
.chakanbaogao .right .content div span {
  width: 40%;
  float: left;
}
.chakanbaogao .right .content div span + span {
  margin-left: 7%;
}
.chakanbaogao .right .title2 {
  font-size: 20px;
  text-align: center;
  color: rgb(68, 68, 68);
  margin-top: 30px;
  margin-bottom: 20px;
}
#main {
  width: 600px;
  height: 400px;
  margin-left: 20px;
}
.huodongye .nm a {
  float: right;
  margin-right: 30px;
  color: rgb(73, 73, 73);
}
